import { getActClass } from './utils';


export default {
  name: 'PressPCLogin',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    showQQ: {
      type: Boolean,
      default: true,
    },
    showWX: {
      type: Boolean,
      default: true,
    },
    destroyedWhenClosed: {
      type: Boolean,
      default: false,
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'close',
    'jumpToLoginQQ',
    'jumpToLoginWX',
    'update:show',
  ],
  data() {
    return {
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
    },
    closeDialog() {
      if (this.destroyedWhenClosed) {
        const dialogId = 'tip-dialog-login';
        const oldDialog = document.getElementById(dialogId);
        if (oldDialog) {
          oldDialog.parentNode.removeChild(oldDialog);
        }
      }

      this.$emit('close');
      this.$emit('update:show', false);
    },
    jumpToLoginQQ() {
      this.$emit('jumpToLoginQQ');
      this.closeDialog();
    },
    jumpToLoginWX() {
      this.closeDialog();
      this.$emit('jumpToLoginWX');
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
