import PressPCLogin from 'press-ui/press-p-c-login/press-p-c-login.vue';


export default {
  components: {
    PressPCLogin,
  },
  data() {
    let show = false;

    show = true;


    return {
      show,
    };
  },
  methods: {
    showDialog() {
      this.show = true;
    },
    jumpToLoginWX() {
      this.onGTip('[jumpToLoginWX]');
      this.show = false;
    },
    jumpToLoginQQ() {
      this.onGTip('[jumpToLoginQQ]');
      this.show = false;
    },
    closeDialog() {
      console.log('[closeDialog]');
    },
  },
};
